.step-three--container {
  background-color: #fff;
  margin-top: 1em;
  padding: 1em;
  border: 2px solid #d3e0fd;
  border-radius: 10px;
}

.step-three--container label:first-child {
  font-size: 18px;
}

.step-three--header-span {
  font-size: 12px;
  color: gray;
}

.step-three--dates-info {
  width: 100%;
  margin-top: 0.5em;
  display: flex;
  align-items: center;
  gap: 1em;
}

.step-three--dates-info label {
  font-size: 14px;
  flex-basis: 100%;

  display: flex;
  flex-direction: column;
  gap: 0.5em;
}

.step-three--dates-info label input[type="date"] {
  padding: 0.75em 1em;
  border-radius: 10px;
  border-color: #d3e0fd;
  outline: none;
}

.step-three--budget-info {
  margin-top: 0.5em;
  width: 100%;
}

.step-three--budget-info label {
  font-size: 14px;

  display: flex;
  flex-direction: column;
}

.step-three--budget-info-range {
  display: flex;
  justify-content: space-between;
}

.step-three--location-info {
  margin-top: 1em;
}

.step-three--location-info-location-type {
  margin-top: 1em;
}

.step-three--location-info-tab-items {
  display: flex;

  padding-left: 0;
  margin-top: 0.25em;
}

.step-three--location-info-tab-items li {
  list-style: none;
  cursor: pointer;
  font-size: 14px;
  padding: 0.5em 2em;
  background-color: rgb(226, 225, 225);
  border-radius: 999px;
}

.step-three--location-info-tabs ul {
  display: flex;

  padding-left: 0;
  margin-top: 0.25em;
}

.step-three--location-info-tabs ul li {
  list-style: none;
  font-size: 14px;
  padding: 0.5em 2em;
  background-color: rgb(226, 225, 225);
  border-radius: 999px;

  position: relative;
}
.step-three--location-info-tabs ul li:first-child {
  border-radius: 999px 0 0 999px;
}

.step-three--location-info-tabs ul li:last-child {
  border-radius: 0 999px 999px 0;
}

.step-three--location-info-tabs ul li.active {
  background-color: #0f6eff;
  color: #fff;
}

.step-three--location-info-tabs ul li:first-child.active::after {
  content: "";
  position: absolute;
  height: 2.18em;
  width: 1.07em;
  left: 100%;
  bottom: 0;
  z-index: 1;

  background-color: #0f6eff;
  border-radius: 0 999px 999px 0;
}

.step-three--location-info-tabs ul li:last-child.active::before {
  content: "";
  position: absolute;
  height: 2.18em;
  width: 1.07em;
  right: 100%;
  bottom: 0;
  z-index: 1;

  background-color: #0f6eff;
  border-radius: 999px 0 0 999px;
}

.step-three--location-info-tab-content {
  width: 100%;
  font-size: 14px;
  margin-top: 1em;
}

.step-three--location-info-tab-content-location {
  width: 100%;
  border: 2px solid #d3e0fd;
  border-radius: 10px;
  padding: 1.1em 1.5rem;
}

.step-three--location-info-tab-content-radius-label {
  display: flex;
  flex-direction: column;
}

.step-three--radius-range {
  margin-top: 0;

  display: flex;
  justify-content: space-between;
}
