.step-four--container {
  background-color: #fff;
  margin-top: 1em;
  padding: 1em;
  border: 2px solid #d3e0fd;
  border-radius: 10px;
}

.step-four--container label:first-child {
  font-size: 18px;
}

.step-four--header-span {
  font-size: 12px;
  color: gray;
}

.step-four--preview-body {
  margin-top: 1em;

  display: flex;
  gap: 1em;
}
/* 
.step-four--preview {
    border: 1px solid rgb(209, 209, 209);
    border-radius: 10px;
    padding: 1em;
} */

.step-four--preview-span {
  border: 1px solid rgb(209, 209, 209);
  border-radius: 10px;
  padding: 1em;
}

.step-four--preview-header {
  display: flex;
  align-items: center;
  gap: 0.5em;
  font-size: 14px;
  margin-bottom: 1rem;
}
.step-four--preview-header p strong {
  font-size: medium;
  color: darkblue;
}
.step-four--preview-header p {
  margin: 0;
  font-size: small;
}
.step-four--image-one {
  width: 2.5em;
}

.step-four--preview-img-section {
  width: 100%;
  height: 5em;
  margin-top: 1rem;
  background-color: aliceblue;
}
.step-four--image-two {
  width: 100%;
  height: 70%;
}

.step-four--preview-img-section p {
  margin: 0;
  font-size: 13px;
  color: darkblue;
  margin-left: 7px;
  font-weight: 500;
}

/* Hide the browser's default radio */
.step-four--preview input[type="radio"] {
  display: none;
}

.step-four--preview
  label
  input[type="radio"]:checked
  + .step-four--preview-span {
  border-color: blue;
}

.step-four--preview-buttons {
  display: flex;
  gap: 1em;

  margin-top: 1em;
}

.step-four--preview-buttons button {
  border-radius: 10px;
  border: none;
  outline: none;
  background-color: #d3e0fd;
  color: #0f6eff;
  padding: 0.5em 1em;

  flex-basis: 100%;
  display: none;
}

.step-four--preview
  label
  input[type="radio"]:checked
  + .step-four--preview-span
  .step-four--preview-buttons
  button {
  display: block;
}
